import React, { Suspense } from "react";

import { Route, Routes } from "react-router-dom";


import Home from "../../pages/Home";
//import Cart from "../../pages/Cart/index"
import CartEmpty from "../CartEmpty/index"
//import NotFound from "../../pages/NotFound";
//import FullPizza from "../FullPizza";
import MainLayout from "../../layouts/MainLayout";

import { useSelector } from "react-redux";

import { selectCart } from "../../redux/cart/selectors";


const Cart = React.lazy(() => import(/*webpackChunkName: "Cart"*/'../../pages/Cart/index'));
const FullPizza = React.lazy(() => import(/*webpackChunkName: "FullPizza"*/'../FullPizza/index'));
const NotFound = React.lazy(() => import(/*webpackChunkName: "NotFound"*/'../../pages/NotFound/index'));








const App: React.FC = () => {




   const { items } = useSelector(selectCart)


   return (

      <Routes>

         <Route path='/' element={<MainLayout />}>

            <Route path='' element={<Home />}></Route>
            {items.length === 0 ?
               <Route path='cart' element={<CartEmpty />}></Route>
               :
               <Route path='cart' element={<Suspense fallback={<div>...</div>}><Cart /></Suspense>}></Route>}

            <Route path='pizza/:id' element={<Suspense fallback={<div> ...</div>}><FullPizza /></Suspense>}></Route>

            <Route path='*' element={<Suspense fallback={<div> ...</div>}><NotFound /></Suspense>}></Route>

         </Route >
      </Routes >


   );
}

export default App;
