
import React from 'react';
import styles from './search.module.css'


import { useDispatch, useSelector } from 'react-redux';
import { inputValue, clearInput } from '../../redux/search/slice';
import { searchSelect } from '../../redux/search/selectors';






const Search: React.FC = () => {

   const inputRef = React.useRef<HTMLInputElement>(null)


   const refreshInput = () => {
      dispatch(clearInput())
      inputRef.current?.focus()
   }

   const onChangeinput = (event: React.ChangeEvent<HTMLInputElement>) => {
      dispatch(inputValue(event.target.value))
   }



   const dispatch = useDispatch();
   const searchValue = useSelector(searchSelect)





   return (
      <div className={styles.root}>
         <img className={styles.img} src="./img/header/search.svg" alt="" />
         <input ref={inputRef} value={searchValue} onChange={onChangeinput} className={styles.input} type='text' placeholder='' />
         {searchValue != '' && <img onClick={refreshInput} className={styles.clear} src="./img/header/clear-input.svg" alt="" />}
      </div>

   );
}

export default Search;