



import { RootState } from '../store';
import axios from 'axios';
import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit'
import { PizzaSliceState, Status, Pizza, LinkParametres } from './types'








const initialState: PizzaSliceState = {
   items: [],
   status: Status.LOADING,
}


export const fetchPizzas = createAsyncThunk<Pizza[], LinkParametres>('pizzas/fetchPizzasStatus', async (params) => {
   const { cakeLinkSort, cakeLinkFilter, currentPage } = params;
   const { data } = await axios.get<Pizza[]>(`https://633c284b74afaef16403361d.mockapi.io/items?page=${currentPage}&limit=4${cakeLinkFilter}${cakeLinkSort}`);
   return data;
});



export const pizzasSlice = createSlice({
   name: 'pizzas',
   initialState,
   reducers: {

   },
   extraReducers: (builder) => {
      builder.addCase(fetchPizzas.pending, (state) => {
         state.status = Status.LOADING;
         state.items = [];

      })
      builder.addCase(fetchPizzas.fulfilled, (state, action: PayloadAction<Pizza[]>) => {
         state.items = action.payload;
         state.status = Status.SUCCESS;

      })
      builder.addCase(fetchPizzas.rejected, (state) => {
         console.log(' ')
         state.status = Status.ERROR;
         state.items = [];

      })

   }
})







export const { } = pizzasSlice.actions

export default pizzasSlice.reducer