

import { RootState } from '../store'

import { createSlice, PayloadAction } from '@reduxjs/toolkit'

import { SortSliceState, SortSliceValue } from './types'











const initialState: SortSliceState = {
   value: { title: ' (ASC)', value: 'rating', key: 'up' },
   status: false,
}



export const sortSlice = createSlice({
   name: 'sort',
   initialState,
   reducers: {
      changeSort: (state, action: PayloadAction<SortSliceValue>) => {
         state.value = action.payload
         state.status = false
      },
      showSort: (state) => {
         state.status = !state.status
      },
      closeSort: (state) => {
         state.status = false
      },
   },
})



export const { changeSort, showSort, closeSort } = sortSlice.actions

export default sortSlice.reducer