import React, { MouseEvent } from 'react';

import { useSelector, useDispatch } from 'react-redux';
import { changeSort, showSort, closeSort } from '../../redux/sort/slice'
import { selectSort } from '../../redux/sort/selectors'



import '../Sort/style.css'





type SortItem = {
   title: string,
   value: 'rating' | 'price' | 'title'
   key: 'up' | 'down'
}


const items: SortItem[] = [
   {
      title: ' (ASC)',
      value: 'rating',
      key: 'up'
   },
   {
      title: ' (DESC)',
      value: 'rating',
      key: 'down'
   },
   {
      title: ' (ASC)',
      value: 'price',
      key: 'up'
   },
   {
      title: ' (DESC)',
      value: 'price',
      key: 'down'
   },
   {
      title: ' (ASC)',
      value: 'title',
      key: 'up'
   },
   {
      title: ' (DESC)',
      value: 'title',
      key: 'down'
   }
]

const Sort: React.FC = React.memo(() => {


   const dispatch = useDispatch();
   const { value, status } = useSelector(selectSort)
   const sortRef = React.useRef<HTMLDivElement>(null);




   React.useEffect(() => {

      const clickOutside = (event: MouseEvent) => {
         const _event = event as MouseEvent & {
            path: Node[];
         }
         if (sortRef.current && !_event.path.includes(sortRef.current)) {
            dispatch(closeSort())

         }
      }

      document.body.addEventListener('click', () => clickOutside)

      return () => document.body.removeEventListener('click', () => clickOutside)
   }, [])



   return (


      <div ref={sortRef} className="main__sort-block sort-block-main">
         <img className={status ? "sort-block-main__img active" : "sort-block-main__img"} src="./img/main/sort-block/Vector.svg" alt="" />
         <span className="sort-block-main__title"> :</span>
         <a href='' onClick={(e) => { dispatch(showSort()); e.preventDefault() }} className="sort-block-main__link">{value.title}</a>

         <div className={status ? "sort-main-block__change change-sort active" : "sort-main-block__change change-sort"}>
            <ul className="change-sort__items">

               {items.map((obj, index) => (
                  <li onClick={() => dispatch(changeSort(obj))} key={index} className={value.title === obj.title ? "change-sort__item active" : "change-sort__item"} > {obj.title}</li>
               ))}

            </ul>
         </div>



      </div >


   );
})

export default Sort;