import React from 'react'
import '../Categories/style.css'

import { useWhyDidYouUpdate } from 'ahooks';


import { useSelector, useDispatch } from 'react-redux'
import { categorySelect } from '../../redux/category/selectors'

type ChangeCategoryFunc = {
   onChangeCategory: (index: number) => void
}


const Categories: React.FC<ChangeCategoryFunc> = React.memo(({ onChangeCategory }) => {

   useWhyDidYouUpdate('Categories', { onChangeCategory })



   const modification = [
      "",
      "",
      "",
      "",
      "",
      "",
   ]




   const dispatch = useDispatch();
   const activeCategory = useSelector(categorySelect)




   return (

      <ul className="nav-block-main__items">
         {modification.map((el, index) => (
            <li onClick={() => onChangeCategory(index)} key={el} className={activeCategory === index ? "nav-block-main__item active" : "nav-block-main__item"}>{el}</li>
         ))
         }

      </ul >




   );
})

export default Categories;