import React from 'react'

import '../../pages/Home/style.css'

import { Link } from 'react-router-dom';

import Card from '../../components/Card';
import Sort from '../../components/Sort';
import Categories from '../../components/Categories';
import Skeleton from '../../components/Card/skeleton';
import Pagintion from '../../components/Card/Pagination';
import Error from '../../components/Error';

import { useSelector } from 'react-redux';

import { pizzasSelect } from '../../redux/pizza/selectors';
import { searchSelect } from '../../redux/search/selectors';

import { categorySelect } from "../../redux/category/selectors";
import { selectSort } from "../../redux/sort/selectors";
import { pageSelect } from "../../redux/currentPage/selectors";
import { fetchPizzas } from "../../redux/pizza/slice";

import { changeCategory } from '../../redux/category/slice'

import { useAppDispatch } from '../../redux/store';






const Home = () => {


   const { value } = useSelector(selectSort)

   const dispath = useAppDispatch();

   const activeCategory = useSelector(categorySelect)
   const currentPage = useSelector(pageSelect)



   React.useEffect(() => {

      //const searchValue = useSelector((state) => state.search.value)
      //const cakeLinkSearch = searchValue != '' ? `&search=${searchValue}` : '';

      const cakeLinkSort = value.key === 'up' ? `&sortBy=${value.value}&order=asc` : `&sortBy=${value.value}&order=desc`;
      const cakeLinkFilter = activeCategory != 0 ? `&category=${activeCategory}` : '';



      dispath(fetchPizzas({
         cakeLinkSort,
         cakeLinkFilter,
         currentPage,
      }))

   }, [currentPage, activeCategory, value])



   const onChangeCategory = React.useCallback((index: number) => {
      dispath(changeCategory(index))
   }, [])
   //const onChangeCategory = (index: number) => {
   //   dispath(changeCategory(index))
   //}




   const { status, items } = useSelector(pizzasSelect)
   const searchValue = useSelector(searchSelect)






   return (

      <main className="main">
         <div className="main__container container">
            <div className="main__nav-block nav-block-main">
               <nav className="nav-block-main__assortiment">

                  <Categories onChangeCategory={onChangeCategory} />

               </nav>

               <Sort />

            </div>


            <div className="main__shop main-shop">


               {status === 'error' ?

                  <Error />

                  :

                  <>
                     <h1 className="main-shop__title"> </h1>
                     <section className="main-shop__items">


                        {status === 'loading' ? [0, 0, 0, 0, 0, 0].map((sklt, index) => (
                           <Skeleton key={index} />
                        )) :
                           items
                              .filter(i => {
                                 if (i.title.toLowerCase().includes(searchValue.toLowerCase())) {
                                    return true
                                 }
                              })
                              .map((obj, index: number) => (
                                 <Card key={index} obj={obj} />
                              ))}



                     </section>

                     <Pagintion />
                  </>

               }

            </div>
         </div>
      </main>



   );
}

export default Home;


