import pizzasSlice from './pizza/slice'
import cartSlice from './cart/slice'
import searchSlice from './search/slice'
import currentPageSlice from './currentPage/slice'

import categorySlice from './category/slice'
import sortSlice from './sort/slice'
import { configureStore, combineReducers } from '@reduxjs/toolkit'

import { useDispatch } from 'react-redux'

import { persistStore, persistReducer, FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER, } from 'redux-persist'
import storage from 'redux-persist/lib/storage'



const rootReducer = combineReducers({
   cart: cartSlice,
   sort: sortSlice,
   categories: categorySlice,
   currentPage: currentPageSlice,
   search: searchSlice,
   pizzas: pizzasSlice,

})

const rootPersistConfig = {
   key: 'data',
   storage,
   blacklist: ['currentPage', 'search']
}

const presistedReducer = persistReducer(rootPersistConfig, rootReducer)


export const store = configureStore({
   reducer: presistedReducer,
   middleware: (getDefaultMiddleware) =>
      getDefaultMiddleware({
         serializableCheck: {
            ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
         },
      }),


})


export const persistor = persistStore(store)


export type RootState = ReturnType<typeof store.getState>;

type AppDispatch = typeof store.dispatch
export const useAppDispatch = () => useDispatch<AppDispatch>()

