import React from 'react';

import '../Card/style.css'

import { Link } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { addItem } from '../../redux/cart/slice'
import { selectCartItemById } from '../../redux/cart/selectors'






const modificationDough = ['', ''];


export type Item = {
   obj: {
      id: string
      imageUrl: string
      title: string
      types: number[]
      sizes: number[]
      price: number
      category: number
      rating: number

   }
}


const Card: React.FC<Item> = ({ obj }) => {


   const [size, setSize] = React.useState(0)
   const [dough, setDough] = React.useState(2)

   const dispath = useDispatch();

   const cartItem = useSelector(selectCartItemById(obj));

   const addedCount = cartItem ? cartItem.count : 0;





   const onClickAdd = () => {
      const item = {
         id: obj.id,
         title: obj.title,
         imageUrl: obj.imageUrl,
         price: obj.price,
         size: size,
         dough: modificationDough[dough],
         count: 1,
      }
      dispath(addItem(item))

   }


   return (

      <div className="main-shop__item item-main-shop">

         <Link to={`pizza/${obj.id}`}>
            <img src={obj.imageUrl} alt="" className="item-main-shop__img" />
         </Link>




         <h2 className="item-main-shop__title">{obj.title}</h2>



         <div className="item-main-shop__modification modification">
            <div className="modification__up">

               {obj.types.map((el, index) => (
                  <button onClick={() => setDough(el)} key={index} className={dough === el ? "modification__btn active" : "modification__btn"}>{modificationDough[el]}</button>
               ))}

            </div>
            <div className="modification__down">

               {obj.sizes.map((el, index) => (
                  <button key={index} onClick={() => setSize(el)} className={size === el ? "modification__btn active" : "modification__btn"}>{el} .</button>
               ))}

            </div>
         </div>
         <div className="item-main-shop__cta-block">
            <span className="item-main-shop__price"> {obj.price}  </span>
            <label className="item-main-shop__btn">
               <svg width="12" height="12" viewBox="0 0 12 12" fill="none"
                  xmlns="http://www.w3.org/2000/svg">
                  <path
                     d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                     fill="#EB5A1E" />
               </svg>
               <input onClick={size === 0 || dough === 2 ? () => window.confirm('  ') : onClickAdd} type="button" />
               {addedCount > 0 && <span className="item-main-shop__number">{addedCount}</span>}
            </label>
         </div>
      </div >



   );
}

export default Card;




