



import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { RootState } from '../store'
//import { saveItemsToLs } from '../../utils/saveItemsToLs'
import { CartItem } from './types'




interface CartSlicaState {
   totalPrice: number
   items: CartItem[]
}




const initialState: CartSlicaState = {
   items: [],
   totalPrice: 0
}




export const cartSlice = createSlice({
   name: 'cart',
   initialState,
   reducers: {

      addItem: (state, action: PayloadAction<CartItem>) => {
         const findItem = state.items.find(obj => obj.id === action.payload.id)
         if (findItem) {
            findItem.count++
         } else {
            state.items.push(action.payload);

         }
         state.totalPrice = state.items.map(i => i.price * i.count).reduce((a, b) => a + b);

      },

      removeItem: (state, action: PayloadAction<string>) => {
         state.items = state.items.filter(el => el.id != action.payload);

         if (state.items.length > 0) {
            state.totalPrice = state.items.map(i => i.price * i.count).reduce((a, b) => a + b);
         } else {
            state.totalPrice = 0;
         }

      },

      clearItems: (state) => {
         state.items = [];
         state.totalPrice = 0;
         window.confirm(' ,    ?')

      },

      reduceItem: (state, action: PayloadAction<CartItem>) => {
         const findItem = state.items.find(obj => obj.id === action.payload.id);
         if (findItem) {
            if (findItem.count > 1) {
               findItem.count--;
               state.totalPrice = state.items.map(i => i.price * i.count).reduce((a, b) => a + b);
               console.log(findItem.count)
            }
         }

      },

   },
})



export const { addItem, removeItem, clearItems, reduceItem } = cartSlice.actions

export default cartSlice.reducer